/**
 * module ini nantinya bisa diakses oleh file js lainnya
 *
 */
export let calculateMonthlyPayment = (principal,years,rate) => {
    let monthlyRate = 0;
    if(rate){
        monthlyRate = rate / 100 / 12;
    }
    var monthlyPayment = principal * monthlyRate / (1-Math.pow(1/(1+monthlyRate),years * 12));
    /**
     * Destructur membantu menghilangkan kebutuhan
     * variabel sementara ketika bekerja dengan objek dan array
     */
    return {principal,years,rate,monthlyPayment,monthlyRate};
};
/**
 * Berikut cara penulisan function pada EcmaScript6 function(paramter) diganti menjadi (parameter) =>
 * @param principal
 * @param years
 * @param rate
 */
export let calculateAmortization = (principal,years,rate) => {
    let {monthlyRate, monthlyPayment} = calculateMonthlyPayment(principal, years, rate);
    let balance = principal;
    let amortization = [];
    for (let y=0; y<years; y++) {
        let interestY = 0;  //Interest payment for year y
        let principalY = 0; //Principal payment for year y
        for (let m=0; m<12; m++) {
            let interestM = balance * monthlyRate;       //Interest payment for month m
            let principalM = monthlyPayment - interestM; //Principal payment for month m
            interestY = interestY + interestM;
            principalY = principalY + principalM;
            balance = balance - principalM;
        }
        amortization.push({principalY, interestY, balance});
    }
    return {monthlyPayment, monthlyRate, amortization};
};


